@import url(https://fonts.googleapis.com/css?family=Quicksand:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.roomList {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.roomList i {
  padding: 10px;
  width: 100%;
  border-radius: 0px 0px 8px 8px;
  color: white;
  background: -webkit-gradient(linear, left bottom, right top, from(#00c6ff), to(#0072ff));
  background: linear-gradient(to top right, #00c6ff, #0072ff);
  -webkit-transition: 100ms ease-in-out;
  transition: 100ms ease-in-out;
}

.roomList i:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.roomList i:active {
  -webkit-transform: scale(1);
          transform: scale(1);
  cursor: pointer;
}

.MessagesContainer {
    display: grid;
    padding: 5px;
    padding-bottom: 50px;
    overflow: scroll;
    height: 74vh;
}

.MessageCont {
    padding: 4px;
    margin-top: 10px;
    display: flex;
}

.MessageCont p {
    margin: 0;
}

.MessageDate {
    color: grey;
    text-align: right;
}

.Sent {
    color: #ffffff;
    background: -webkit-gradient(linear, left bottom, right top, from(#00c6ff), to(#0072ff));
    background: linear-gradient(to top right, #00c6ff, #0072ff);
    border-radius: 15px 15px 0px 15px;
    padding: 10px;
}

.Recieved {
    background: #d8d8d8;
    border-radius: 15px 15px 15px 0px;
    padding: 10px;
}

.Left {
    text-align: left;
    justify-self: left;
}

.Right {
    text-align: left;
    justify-self: right;
    flex-flow: row-reverse;
}


li {
    max-width: 80%
}

.MessageAvatar {
    height: 35px;
    width: 35px;
}

.userInfo {
    padding: 5px;
}

.MessageFader {
    z-index: 10;
    color: rgba(255, 255, 255, 0);
    border: 1px solid black;
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,1)), color-stop(60%, rgba(255,255,255,1)), to(rgba(255,255,255,0)));
    background: linear-gradient(to top, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 60%, rgba(255,255,255,0) 100%);
    background: -moz-linear-gradient(to top, rgba(138, 15, 15, 1) 0%, rgba(255,255,255,1) 60%, rgba(255,255,255,0) 100%);
}

@media screen and (max-width: 360px) {
    .MessageCont p {
        overflow-wrap: break-word;
        max-width: 260px;
    }

    .MessagesContainer {
        height: 61vh;
    }

  }

  @media screen and (max-width: 375px) {
    .MessageCont p {
        overflow-wrap: break-word;
        max-width: 260px;
    }

    .MessagesContainer {
        height: 57vh;
    }

  }


  @media screen and (max-width: 414px) {
    .MessageCont p {
        overflow-wrap: break-word;
        max-width: 260px;
    }

    .MessagesContainer {
        height: 66vh;
    }

  }

  @media screen and (min-height: 896px) and (max-height: 900px) {
    .MessagesContainer {
        height: 72vh;
    }

  }

.SendContainer {
    position: fixed;
    bottom: 0;
    height: 40px;
    width: 100vw;
    background: #c5c5c5;
    text-align: left;
    display: flex;
    justify-content: center;
}

.sendBar {
    width: 80%;
    text-align: left;
    border-radius: 10px;
    border: none;
    margin: 4px;
    padding: 2px;
    padding-left: 20px;
    font-family: 'Quicksand';
    font-weight: 500;
}

.sendButton {
    font-size: 25px;
    align-self: center;
    color: #0261e5;
    margin-left: 30px;
}

.sendButton:hover {
    color: #029ae5;
}
.UserStatus {
    border-radius: 15px;
    height: 20px;
    width: 120px;
    margin: 2px;
    padding: 2px;
}

.UserStatus p {
    margin: 0;
    font-family: 'Quicksand';
    font-weight: 700;
    color: #ffffff;
}

.UserContainer {
    display: flex;
    justify-content: center;
}

.online {
    border: 2px solid #1b8f5d;
    background: #24C157;
}

.offline {
    border: 2px solid #c12440;
    background: #94241c;
}

@media screen and (max-width: 550px) {
    .UserContainer {
        max-height: 30px;
        overflow-y: scroll;
        flex-wrap: wrap;
    }
}
.RoomContainer {
    font-family: "Quicksand"
}
.login {
  display: flex;
  flex-direction: column;
  width: 300px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 20px;
  font-family: 'Quicksand';
  font-weight: 700;
}
.login input {
  text-align: center;
  margin: 20px;
  border-radius: 8px;
  outline: hidden;
  height: 30px;
  font-size: 20px;
  font-family: 'Quicksand';
}

.login input:focus {
  outline: none;
}

.login i {
  margin: 20px;
  background: -webkit-gradient(linear, left bottom, right top, from(#00c6ff), to(#0072ff));
  background: linear-gradient(to top right, #00c6ff, #0072ff);
  /* border: 2px black solid; */
  border-radius: 8px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: 100ms ease-in-out;
  transition: 100ms ease-in-out;
  /* height: 30px; */
  text-align: center;
  padding: 10px;
  color: white;
  font-size: 20px;
}

.login i:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.login i:active {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  cursor: pointer;
}

.login .true {
  -webkit-animation-name: shake;
          animation-name: shake;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

@-webkit-keyframes shake {
  0% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  20% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  40% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  60% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  80% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

@keyframes shake {
  0% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  20% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  40% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  60% {
    -webkit-transform: translateX(-10px);
            transform: translateX(-10px);
  }
  80% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}

